@import './variables.sass';

.note-wrapper {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	a {
		display: flex;
		width: 100%;
		justify-content: center; }
	.no-notes {
		height: 100%;
		width: 100%;
		min-height: calc(100vh - 120px);
		p {
			margin-bottom: 10vh;
			font: {
				size: 1.4rem;
				weight: 700; } } } }
