@import '../../view_styles/variables.sass';
.category-setter {
	min-width: 320px;
	width: 320px;
	height: 40px;
	position: relative;
	background-color: $tertiary-color;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 2;
	&:hover {
		cursor: pointer; } }
.category__list {
	position: relative;
	top: 60px;
	left: -320px;
	width: 320px;
	min-width: 320px;
	background-color: red;
	box-shadow: 0 0 13px 3px rgba(0,0,0,0.3); }
.category__item {
	border-top: 1px solid rgba(55,55,55,0.2);
	background-color: rgba(255,255,255,.3);
	padding: 10px;
	transition: all .12s ease-in-out;
	&:hover {
		background-color: #fff;
		cursor: pointer; } }

@media all and (max-width: 360px) {
	.category__list {
		top: 100px; } }
